
























import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import {VideoList} from '@/models/VideoList'
import {Player} from '@/models/Player'
import {PlayerState} from '@/models/Constants'

@Component({components: {}})
export default class Youtube extends Vue {
  player1: Player = null;
  player2: Player = null;
  firstPlayerIsActive: boolean = true
  autoPlay: boolean = false
  loadYT = null
  queryString = location.hash.replace('#', '?')
  // queryString = location.search ? location.search : '?a=1&v=E5i4rDc_GA8.20.60'
  videoList: VideoList = VideoList.fromString(this.queryString)


  mounted() {
    this.autoPlay = this.videoList.autoPlay
    if (!this.loadYT) {
      this.loadYT = new Promise((resolve) => {
        const tag = document.createElement('script');
        tag.src = 'https://www.youtube.com/iframe_api';
        const firstScriptTag = document.getElementsByTagName('script')[0];
        firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
        //@ts-ignore
        window.onYouTubeIframeAPIReady = () => resolve(window.YT);
      });
    }
    this.loadYT.then((YT) => {
      this.player1 = new Player(YT, 1, this.$refs.player1, this.onEvent,
        {
          videoId: this.videoList.firstVideoId(),
          autoPlay: this.autoPlay,
        },
      )
      const firstVideoIdForSecondPlayer = this.videoList.firstVideoIdForSecondPlayer()
      if (firstVideoIdForSecondPlayer) {
        this.player2 = new Player(YT, 2, this.$refs.player2, this.onEvent,
          {
            videoId: firstVideoIdForSecondPlayer,
            autoPlay: false,
          },
        )
      }
    });
  }

  onEvent(event) {
    const {state, id, info} = event

    let video
    switch (state) {
      case PlayerState.READY:
        this.logState(id, state, info)
        if (id == 1) {
          video = this.videoList.next();
          if (video.videoId === this.activePlayer.currentVideoId) {
            this.activePlayer.seek(video)
          } else {
            console.log(57624588857567)
          }
        }
        break
      case PlayerState.ENDED:
        this.logState(id, state, info)
        video = this.videoList.next();
        if (video.videoId === this.activePlayer.currentVideoId) {
          this.activePlayer.seek(video)
        } else {
          this.changePlayerActivity()
          // this.passivePlayer.stop()
          this.activePlayer.seek(video)
        }
        break
      default:
    }
  }

  logState(id, playerState, info = '') {
    // console.log('logState', playerState)
    console.log(id, Object.keys(PlayerState).find(key => PlayerState[key] === playerState))
  }

  get activePlayer(): Player {
    return this.firstPlayerIsActive ? this.player1 : this.player2
  }

  get passivePlayer(): Player {
    return this.firstPlayerIsActive ? this.player2 : this.player1
  }

  changePlayerActivity() {
    this.firstPlayerIsActive = !this.firstPlayerIsActive
  }

  get marginLeft1() {
    return this.firstPlayerIsActive ? '0px' : '-2000px'
  }

  get marginLeft2() {
    return !this.firstPlayerIsActive ? '0px' : '2000px'
  }

  get ytPlayer1() {

    return {
      // display: this.firstPlayerIsActive
      // marginTop: this.firstPlayerIsActive? '0px' : '1000px',
      "z-index": this.firstPlayerIsActive ? 10 : 1,
    }
  }

  get ytPlayer2() {
    return {
      // marginTop:'100px'
      // display: !this.firstPlayerIsActive
      // marginTop: !this.firstPlayerIsActive? '0px' : '1000px',
      "z-index": this.firstPlayerIsActive ? 1 : 10,
    }
  }

};
